import React, {useState, useEffect} from 'react';
import PropTypes from 'prop-types';
import {Container, FormControl, Grid, InputLabel, MenuItem, Select,BottomNavigation, Paper, Divider, SvgIcon, Collapse, Checkbox, FormControlLabel, BottomNavigationAction} from '@mui/material';
import { InvertColorsOff, SaveOutlined} from '@mui/icons-material';
import { alpha, styled } from '@mui/material/styles'
import TreeView from '@mui/lab/TreeView';
import TreeItem, { treeItemClasses } from '@mui/lab/TreeItem';
import { useSpring, animated } from 'react-spring';

import { Box } from '@mui/system';
import style from "../../tools/Style";
import { useStateValue } from '../../context/store';
import { useParams, useHistory } from 'react-router-dom';

 import {getListarPlan} from "../../Services/PlanesModuloService";
 import {getListarMenu} from "../../Services/PlanesModuloService";
 import {postGuardarPlanModulo} from "../../Services/PlanesModuloService";


const FormCreatePlanModulo = () => {
    let history = useHistory();

    const [{sesionUsuario}, dispatch]  = useStateValue();
    const [selected, setSelected] = React.useState([]);
    const {id} = useParams();

    const [model1, setModel1] = useState({
        idPlan:'',
        listaPlan:[]
    });  
    
    const [model2, setModel2] = useState({
        idPlan:'',
        listaMenu:[]
    });  

    const modelMemoria = e =>{
        const {name, value} = e.target;
        setModel1(anterior=>({
            ...anterior,
            [name]:value
        }));

        btnListarMenu(e.target.value);
    }
    
    function MinusSquare(props) {
        return (
            <SvgIcon fontSize="inherit" style={{ width: 14, height: 14 }} {...props}>
            {/* tslint:disable-next-line: max-line-length */}
            <path d="M22.047 22.074v0 0-20.147 0h-20.12v0 20.147 0h20.12zM22.047 24h-20.12q-.803 0-1.365-.562t-.562-1.365v-20.147q0-.776.562-1.351t1.365-.575h20.147q.776 0 1.351.575t.575 1.351v20.147q0 .803-.575 1.365t-1.378.562v0zM17.873 11.023h-11.826q-.375 0-.669.281t-.294.682v0q0 .401.294 .682t.669.281h11.826q.375 0 .669-.281t.294-.682v0q0-.401-.294-.682t-.669-.281z" />
            </SvgIcon>
        );
    }
    
    function PlusSquare(props) {
        return (
            <SvgIcon fontSize="inherit" style={{ width: 14, height: 14 }} {...props}>
            {/* tslint:disable-next-line: max-line-length */}
            <path d="M22.047 22.074v0 0-20.147 0h-20.12v0 20.147 0h20.12zM22.047 24h-20.12q-.803 0-1.365-.562t-.562-1.365v-20.147q0-.776.562-1.351t1.365-.575h20.147q.776 0 1.351.575t.575 1.351v20.147q0 .803-.575 1.365t-1.378.562v0zM17.873 12.977h-4.923v4.896q0 .401-.281.682t-.682.281v0q-.375 0-.669-.281t-.294-.682v-4.896h-4.923q-.401 0-.682-.294t-.281-.669v0q0-.401.281-.682t.682-.281h4.923v-4.896q0-.401.294-.682t.669-.281v0q.401 0 .682.281t.281.682v4.896h4.923q.401 0 .682.281t.281.682v0q0 .375-.281.669t-.682.294z" />
            </SvgIcon>
        );
    }

    function TransitionComponent(props) {
        const style = useSpring({
            from: {
            opacity: 0,
            transform: 'translate3d(20px,0,0)',
            },
            to: {
            opacity: props.in ? 1 : 0,
            transform: `translate3d(${props.in ? 0 : 20}px,0,0)`,
            },
        });
        
        return (
            <animated.div style={style}>
            <Collapse {...props} />
            </animated.div>
        );
    }
    
    TransitionComponent.propTypes = {
    in: PropTypes.bool,
    };

    const StyledTreeItem = styled((props) => (
        <TreeItem {...props} TransitionComponent={TransitionComponent} />
      ))(({ theme }) => ({
        [`& .${treeItemClasses.iconContainer}`]: {
          '& .close': {
            opacity: 0.3,
          },
        },
        [`& .${treeItemClasses.group}`]: {
          marginLeft: 15,
          paddingLeft: 18,
          borderLeft: `1px dashed ${alpha(theme.palette.text.primary, 0.4)}`,
        },
    }));
    
    function getOnChange(checked, menu) {
        const allNode = [] = getChildById(checked, menu, menu.codigoOpcion);
        let array = checked
        ? [...selected, ...allNode]
        : selected.filter(value => !allNode.includes(value));

        setSelected(array);
    }

    function getChildById(checked, menu, id) {
        let array = [];
        let codigo= '';
        let number = 0;

        if(menu.idOpcionPadre != null && checked) {
            if(menu.idOpcionPadre.length >= 2){
                array.push(menu.idOpcionPadre);
    
                number = menu.idOpcionPadre.length - 2;
                codigo = menu.idOpcionPadre.slice(0, number); 
    
                if(codigo.length >= 2){
                     array.push(codigo);
                    
                     number = codigo.length - 2;
                     codigo = codigo.slice(0, number); 
    
                     if(codigo.length >= 2){
                        array.push(codigo);
                    
                        number = codigo.length - 2;
                        codigo = codigo.slice(0, number);  
    
                        if(codigo.length >= 2){
                            array.push(codigo);
                        }
                     }
                }
            }
        }        

        function getAllChild(menu) {
            if (menu === null) return [];

            array.push(menu.codigoOpcion);

            if (Array.isArray(menu.listaMenu)) {
                menu.listaMenu.forEach(node => {
                    array = [...array, ...getAllChild(node)];
                    array = array.filter((v, i) => array.indexOf(v) === i);
                });
            }
            return array;
        }

        function getNodeById(menu, id) {
            if (menu.codigoOpcion === id) {
                return menu;
            } else if (Array.isArray(menu.listaMenu)) {
                let result = null;
                menu.listaMenu.forEach(node => {
                    if (!!getNodeById(node, id)) {
                        result = getNodeById(node, id);
                    }
                });
                return result;
            }

            return null;
        }

        return getAllChild(getNodeById(menu, id));
    }

    const btnGuardar = e =>{   
        e.preventDefault(); 
        dispatch({type : "OPEN_BACKDROP", open : true}) 
        const objetoModel = {
            idPlan: model1.idPlan,
            listaCodigoMenu: selected
        };        
        postGuardarPlanModulo(objetoModel).then(response =>{           
            dispatch({type : "OPEN_BACKDROP", open : false}) 
            dispatch({
                    type : "OPEN_SNACKBAR",
                    openMensaje : {
                        open : true,
                        mensaje : response.data.mensaje,
                        tipoRespuesta:response.data.tipoRespuesta
                    }
                })
        });
    }

    const btnListarPlan =()=>{ 
        getListarPlan(model1).then(response =>{            
            if(response.status === 200) {
                setModel1(response.data);
                if(response.data.idPlan!=null){
                    btnListarMenu(response.data.idPlan)
                }               
            }else{
                dispatch({
                    type : "OPEN_SNACKBAR",
                    openMensaje : {
                        open : true,
                        mensaje : "Hubo un problema en el sistema web",
                        tipoRespuesta: 0
                    }
                })
            }            
        }); 
    }

    const btnListarMenu =(id)=>{ 
        dispatch({type : "OPEN_BACKDROP", open : true}) 
        model2.idPlan = id;
        getListarMenu(model2).then(response =>{            
            if(response.status === 200) {
                setModel2(response.data);
                setSelected(response.data.listaCodigoMenu);
                dispatch({type : "OPEN_BACKDROP", open : false}) 
            }else{
                dispatch({type : "OPEN_BACKDROP", open : false}) 
                dispatch({
                    type : "OPEN_SNACKBAR",
                    openMensaje : {
                        open : true,
                        mensaje : "Hubo un problema en el sistema web",
                        tipoRespuesta: 0
                    }
                })
            }            
        }); 
    }

    useEffect(() => {
        btnListarPlan();    
    }, []);

    return (
        <Box style={{width:"100%"}}>
            <Paper style={{zIndex: '100'}} sx={style.paperboton} elevation={3}>
                <BottomNavigation showLabels style={{paddingTop: '8px'}}>
                    <BottomNavigationAction style={{color: '#274c5e'}} label="Guardar" icon={<SaveOutlined color="primary"/>} type="submit" onClick={btnGuardar} />           
                </BottomNavigation>
            </Paper>
    
            <div style={style.form}> 
                <Divider>
                    <h3 style={{color: '#1976d2'}}>PLAN MODULO</h3> 
                </Divider>                  
                         
                <Container>  
                    <form>
                        <Grid container spacing={1}>
                            <Grid item xs={12} md={12} sm={12}>
                                <FormControl fullWidth>
                                    <InputLabel id="demo-simple-select-label">Plan</InputLabel>
                                    <Select variant="outlined" name="idPlan" value={model1.idPlan} label="Tipo Base Dato" onChange={modelMemoria}>
                                    {model1.listaPlan.map((name) => (
                                        <MenuItem
                                            key={name.idPlan}
                                            value={name.idPlan}
                                            >
                                            {name.nombrePlan}
                                        </MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                            </Grid> 
                            <Grid item xs={12} md={12} sm={12}>
                            <Divider></Divider>
                                <TreeView   
                                    fullWidth   
                                    aria-label="customized"
                                    defaultExpanded={['1']}
                                    defaultCollapseIcon={<MinusSquare />}
                                    defaultExpandIcon={<PlusSquare />}
                                    sx={{ height: 800, flexGrow: 1, overflowY: 'auto', paddingTop:'10px' }}
                                    >
                                    {model2.listaMenu==null? null: model2.listaMenu.map((row1) =>(                                        
                                        <StyledTreeItem 
                                            key={row1.idOpcion}
                                            nodeId={row1.idOpcion} 
                                            label={
                                                <FormControlLabel
                                                control={
                                                    <Checkbox
                                                        checked={selected.some(item => item === row1.codigoOpcion)}
                                                        onChange={event =>getOnChange(event.currentTarget.checked, row1)}
                                                        onClick={e => e.stopPropagation()}
                                                    />
                                                }
                                                label={<>{row1.nombre}</>}
                                                key={row1.idOpcion}
                                                />
                                            }
                                        >

                                            {row1.listaMenu==null? null: row1.listaMenu.map((row2) =>(                                        
                                                <StyledTreeItem 
                                                    key={row2.idOpcion}
                                                    nodeId={row2.idOpcion} 
                                                    label={
                                                    <FormControlLabel
                                                    control={
                                                        <Checkbox
                                                            checked={selected.some(item => item === row2.codigoOpcion)}
                                                            onChange={event =>getOnChange(event.currentTarget.checked, row2)}
                                                            onClick={e => e.stopPropagation()}
                                                        />
                                                    }
                                                    label={<>{row2.nombre}</>}
                                                    key={row2.idOpcion}
                                                    />
                                                    }
                                                >

                                                    {row2.listaMenu==null? null: row2.listaMenu.map((row3) =>(                                        
                                                        <StyledTreeItem 
                                                            key={row3.idOpcion}
                                                            nodeId={row3.idOpcion} 
                                                            label={
                                                            <FormControlLabel
                                                            control={
                                                                <Checkbox
                                                                    checked={selected.some(item => item === row3.codigoOpcion)}
                                                                    onChange={event =>getOnChange(event.currentTarget.checked, row3)}
                                                                    onClick={e => e.stopPropagation()}
                                                                />
                                                            }
                                                            label={<>{row3.nombre}</>}
                                                            key={row3.idOpcion}
                                                            />
                                                            }
                                                        >

                                                            {row3.listaMenu==null? null: row3.listaMenu.map((row4) =>(                                        
                                                                <StyledTreeItem 
                                                                    key={row4.idOpcion}
                                                                    nodeId={row4.idOpcion} 
                                                                    label={
                                                                    <FormControlLabel
                                                                    control={
                                                                        <Checkbox
                                                                            checked={selected.some(item => item === row4.codigoOpcion)}
                                                                            onChange={event =>getOnChange(event.currentTarget.checked, row4)}
                                                                            onClick={e => e.stopPropagation()}
                                                                        />
                                                                    }
                                                                    label={<>{row4.nombre}</>}
                                                                    key={row4.idOpcion}
                                                                    />
                                                                    }
                                                                >
                                                                    
                                                                    {row4.listaMenu==null? null: row4.listaMenu.map((row5) =>(                                        
                                                                        <StyledTreeItem
                                                                            key={row5.idOpcion}
                                                                            nodeId={row5.idOpcion} 
                                                                            label={
                                                                            <FormControlLabel
                                                                            control={
                                                                                <Checkbox
                                                                                    checked={selected.some(item => item === row5.codigoOpcion)}
                                                                                    onChange={event =>getOnChange(event.currentTarget.checked, row5)}
                                                                                    onClick={e => e.stopPropagation()}
                                                                                />
                                                                            }
                                                                            label={<>{row5.nombre}</>}
                                                                            key={row5.idOpcion}
                                                                            />
                                                                            }
                                                                        >
                                                                        </StyledTreeItem>
                                                                    ))}
                                                                </StyledTreeItem>
                                                            ))}
                                                        </StyledTreeItem>
                                                    ))}
                                                </StyledTreeItem>
                                            ))}
                                        </StyledTreeItem>
                                    ))}
                                    
                                </TreeView>
                            </Grid> 
                           
                            <Grid item xs={12} md={12}>
                            </Grid>
                        </Grid>                       
                    </form>
                </Container>
            </div>   
        </Box>      
      );
    };

export default FormCreatePlanModulo;