import {useState, useEffect} from 'react';
import {Container, Grid, TextField, Paper, BottomNavigation, BottomNavigationAction, Divider} from '@mui/material';
import { SaveOutlined, ArrowBackOutlined, AddCircleOutline} from '@mui/icons-material';
import style from "../../tools/Style";
import { useStateValue } from '../../context/store';
import { useParams, useHistory } from 'react-router-dom';

import {postGuardarPlan} from "../../Services/PlanService";
import {getObtenerPlan} from "../../Services/PlanService";
import { Box } from '@mui/system';

const FormCrearPlan = () =>{
    let history = useHistory();
    const [{sesionUsuario}, dispatch]  = useStateValue();
    const {id} = useParams();

    const [model, setModel] = useState({
        idPlan:'',
        codigoPlan:'',
        nombrePlan:'',
        nroLicencia: 0
    });

    const modelMemoria = e =>{
        const {name, value} = e.target;
        setModel(anterior=>({
            ...anterior,
            [name]:value
        }));
    }    

    const btnGuardar = e =>{
        e.preventDefault();
        dispatch({type : "OPEN_BACKDROP", open : true}) 
        const objetoModel = {
            idPlan: model.idPlan,
            codigoPlan : model.codigoPlan,
            nombrePlan : model.nombrePlan,
            nroLicencia: parseInt(model.nroLicencia || 0)
        };       
        postGuardarPlan(objetoModel).then(response =>{ 
            dispatch({type : "OPEN_BACKDROP", open : false})            
            if(response.status === 200) {
                btnRegresar();
            }
            dispatch({
                    type : "OPEN_SNACKBAR",
                    openMensaje : {
                        open : true,
                        mensaje : response.data.mensaje,
                        tipoRespuesta:response.data.tipoRespuesta
                    }
                })
            });
        }

    const btnObtener =()=>{ 
        if(id!=undefined){
            model.id= (id==undefined)? "": id;
            getObtenerPlan(model).then(response =>{            
                if(response.status === 200) {
                    setModel(response.data);
                }else{
                    dispatch({
                        type : "OPEN_SNACKBAR",
                        openMensaje : {
                            open : true,
                            mensaje : "Hubo un problema en el sistema web",
                            tipoRespuesta: 0
                        }
                    })
                }            
            });
        }         
    }

    const btnRegresar =()=>{
        history.push("/plan");
    }

    useEffect(() => {
        btnObtener();    
    }, []);

    return (
        <Box style={{width:"100%"}}>
            <Paper style={{zIndex: '100'}} sx={style.paperboton} elevation={3}>
                <BottomNavigation showLabels style={{paddingTop: '8px'}}>
                    <BottomNavigationAction style={{color: '#274c5e'}} label="Regresar" icon={<ArrowBackOutlined color="secondary"/>} onClick={btnRegresar} />
                    <BottomNavigationAction style={{color: '#274c5e'}} label="Nuevo" icon={<AddCircleOutline color="success"/>} />
                    <BottomNavigationAction style={{color: '#274c5e'}} label="Guardar" icon={<SaveOutlined color="primary"/>} type="submit" onClick={btnGuardar} />           
                </BottomNavigation>
            </Paper>

            <div style={style.form}> 
                <Divider>
                    <h3 style={{color: '#1976d2'}}>PLAN</h3> 
                </Divider>
                <Container style={style.paper}>  
                    <form>
                        <Grid container spacing={2}>
                            <Grid item xs={12} md={12}>
                                <TextField name="codigoPlan" value={model.codigoPlan} onChange={modelMemoria} variant="outlined" fullWidth label="Cdigo Plan"/>
                            </Grid>    
                            <Grid item xs={12} md={12}>
                                <TextField name="nombrePlan" value={model.nombrePlan} onChange={modelMemoria} variant="outlined" fullWidth label="Nombre Plan"/>
                            </Grid> 
                            <Grid item xs={12} md={12}>
                                <TextField name="nroLicencia" value={model.nroLicencia} onChange={modelMemoria} variant="outlined" fullWidth label="Nro Licencia"/>
                            </Grid>    
                            <Grid item xs={12} md={12}>
                            </Grid> 
                        </Grid>                  
                    </form>               
            </Container>
            </div>  
         </Box> 
    )
};

export default FormCrearPlan;