import React from 'react';
import { List, ListItem, ListItemText, Divider, Avatar } from '@mui/material';
import { Link } from 'react-router-dom';
import FotoUsuario from "../../../logo.svg"

export const MenuIzquierda = ({classes}) => (
    <div className = {classes.list}>
        <List style={{background:"#0059b3"}}>
            <ListItem>
                <Avatar src={ FotoUsuario }/>
                <ListItemText primary="Dinamo Solutions" style={{color:"white"}} />
            </ListItem>
        </List>
        <Divider />
        <List>
            <ListItem component={Link} button to="/usuario">
                <i className="material-icons">account_box</i>
                <ListItemText classes={{primary: classes.listItemText}} primary="Usuario" />
            </ListItem>
            <ListItem component={Link} button to="/plan">
                <i className="material-icons">add_box</i>
                <ListItemText classes={{primary: classes.listItemText}} primary="Plan" />
            </ListItem>
            <ListItem component={Link} button to="/empresa">
                <i className="material-icons">add_box</i>
                <ListItemText classes={{primary: classes.listItemText}} primary="Empresa" />
            </ListItem>
            <ListItem component={Link} button to="/planesmodulo">
                <i className="material-icons">add_box</i>
                <ListItemText classes={{primary: classes.listItemText}} primary="Plan Modulo" />
            </ListItem>
            <ListItem component={Link} button to="/contrato">
                <i className="material-icons">add_box</i>
                <ListItemText classes={{primary: classes.listItemText}} primary="Contrato" />
            </ListItem>
        </List>
      
    </div>
);