import {useState, useEffect} from 'react';
import {Container, FormControl, Grid, InputLabel, MenuItem, Select, TextField,BottomNavigation,BottomNavigationAction, Paper, Divider} from '@mui/material';
import { SaveOutlined, ArrowBackOutlined, AddCircleOutline} from '@mui/icons-material';
import { Box } from '@mui/system';
import style from "../../tools/Style";
import { useStateValue } from '../../context/store';
import { useParams, useHistory } from 'react-router-dom';

 import {postGuardarEmpresa} from "../../Services/EmpresaService";
 import {getObtenerEmpresa} from "../../Services/EmpresaService";


const FormCreateEmpresa = () => {
    let history = useHistory();
    const [{sesionUsuario}, dispatch]  = useStateValue();
    const {id} = useParams();

    const [model, setModel] = useState({
        idSociedad :'',  
        razSoc:'',
        nombComercial:'',
        direccion: '',
        ruc: '',
        codigoSociedad: '',
        tipoEmpresa: '',
        condicion:'',
        tipoDB: '',
        nombreServer: '',
        usuarioServer: '',
        claveServer: '',
        puerto: '',
        nombreDB: '',
        listaTipoBaseDato:[],
        listaTipoEmpresa:[]
    });    

    const modelMemoria = e =>{
        const {name, value} = e.target;
        setModel(anterior=>({
            ...anterior,
            [name]:value
        }));
    }
    
    const btnGuardar = e =>{
        e.preventDefault();
        dispatch({type : "OPEN_BACKDROP", open : true})        
        postGuardarEmpresa(model).then(response =>{  
            dispatch({type : "OPEN_BACKDROP", open : false})          
            if(response.status === 200) {
                btnRegresar();
            }
            dispatch({
                    type : "OPEN_SNACKBAR",
                    openMensaje : {
                        open : true,
                        mensaje : response.data.mensaje,
                        tipoRespuesta:response.data.tipoRespuesta
                    }
                })
            });
        }

    const btnObtener =()=>{ 
        model.id= (id==undefined || id==0)? 0: id;
        getObtenerEmpresa(model).then(response =>{            
            if(response.status === 200) {
                setModel(response.data);
            }else{
                dispatch({
                    type : "OPEN_SNACKBAR",
                    openMensaje : {
                        open : true,
                        mensaje : "Hubo un problema en el sistema web",
                        tipoRespuesta: 0
                    }
                })
            }            
        }); 
    }

    const btnRegresar =()=>{
        history.push("/empresa");
    }

    useEffect(() => {
        btnObtener();    
    }, []);

    // const fotoKey = uuidv4();

    return (
        <Box style={{width:"100%"}}>
            <Paper style={{zIndex: '100'}} sx={style.paperboton} elevation={3}>
                <BottomNavigation showLabels style={{paddingTop: '8px'}}>
                    <BottomNavigationAction style={{color: '#274c5e'}} label="Regresar" icon={<ArrowBackOutlined color="secondary"/>} onClick={btnRegresar} />
                    <BottomNavigationAction style={{color: '#274c5e'}} label="Nuevo" icon={<AddCircleOutline color="success"/>} />
                    <BottomNavigationAction style={{color: '#274c5e'}} label="Guardar" icon={<SaveOutlined color="primary"/>} type="submit" onClick={btnGuardar} />           
                </BottomNavigation>
            </Paper>
    
            <div style={style.form}> 
                <Divider>
                    <h3 style={{color: '#1976d2'}}>EMPRESA</h3> 
                </Divider>         
                <Container style={style.paper}>  
                    {/* <Avatar style={style.avatar} src={usuario.fotoUrl || reactFoto} /> */}              
                    <form>
                        <Grid container spacing={1}>

                            <Grid item xs={12} md={12}>
                                <TextField name="razSoc" value={model.razSoc} onChange={modelMemoria} variant="outlined" fullWidth label="Razn Social"/>
                            </Grid>    
                            <Grid item xs={12} md={12}>
                                <TextField name="nombComercial" value={model.nombComercial} onChange={modelMemoria} variant="outlined" fullWidth label="Nombre Comercial"/>
                            </Grid> 
                            <Grid item xs={12} md={12}>
                                <TextField name="direccion" value={model.direccion} onChange={modelMemoria} variant="outlined" fullWidth label="Direccin"/>
                            </Grid>    
                            <Grid item xs={12} md={6}>
                                <TextField name="ruc" value={model.ruc} onChange={modelMemoria} variant="outlined" fullWidth label="Ruc"/>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <TextField name="codigoSociedad" value={model.codigoSociedad} onChange={modelMemoria} variant="outlined" fullWidth label="Cdigo Sociedad"/>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <FormControl fullWidth>
                                    <InputLabel id="demo-simple-select-label">Tipo Empresa</InputLabel>
                                    <Select variant="outlined" name="tipoEmpresa" value={model.tipoEmpresa} label="Tipo Base Dato" onChange={modelMemoria}>
                                    {model.listaTipoEmpresa.map((name) => (
                                        <MenuItem
                                            key={name.value}
                                            value={name.value}
                                            >
                                            {name.text}
                                        </MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <TextField name="condicion" value={model.condicion} onChange={modelMemoria} variant="outlined" fullWidth label="Condicin Empresa"/>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <FormControl fullWidth>
                                    <InputLabel id="demo-simple-select-label">Tipo Base Dato</InputLabel>
                                    <Select variant="outlined" name="tipoDB" value={model.tipoDB} label="Tipo Base Dato" onChange={modelMemoria}>
                                    {model.listaTipoBaseDato.map((name) => (
                                        <MenuItem
                                            key={name.value}
                                            value={name.value}
                                            >
                                            {name.text}
                                        </MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <TextField name="nombreServer" value={model.nombreServer} onChange={modelMemoria} variant="outlined" fullWidth label="Nombre Servidor"/>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <TextField name="usuarioServer" value={model.usuarioServer} onChange={modelMemoria} variant="outlined" fullWidth label="Usuario Servidor"/>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <TextField name="claveServer" value={model.claveServer} onChange={modelMemoria} variant="outlined" fullWidth label="Clave Servidor"/>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <TextField name="puerto" value={model.puerto} onChange={modelMemoria} variant="outlined" fullWidth label="Puerto"/>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <TextField name="nombreDB" value={model.nombreDB} onChange={modelMemoria} variant="outlined" fullWidth label="Nombre Base Dato"/>
                            </Grid>
                            <Grid item xs={12} md={12}>
                            {/* <ImageUploader
                                withIcon={false}
                                key={fotoKey}
                                singleImage={true}
                                buttonText="Seleccione una imagen de perfil"
                                onChange={subirFoto}
                                imgExtension={[".jpg", ".gif", ".png", ".jpeg"]}
                                maxFileSize={5242880}
                            /> */}
                            </Grid>
                        </Grid>                       
                    </form>
                </Container>
            </div>   
        </Box>      
      );
    };

export default FormCreateEmpresa;