import httpCliente from '../http/httpCliente';

export const getListarEmpresa = model =>{
    return new Promise((resolve, eject) => {
        httpCliente.get('/empresa/getListarEmpresa?'+ `page=${model.page}&pages=${model.pages}&request=${model.request}`)
        .then(response=>{
            resolve(response);
        }).catch(error=>{
            resolve(error.response);
        });
    });
}

export const getObtenerEmpresa = model =>{
    return new Promise((resolve, eject) => {
        httpCliente.get('/empresa/getObtenerEmpresa?'+ `id=${model.id}`)
        .then(response=>{
            resolve(response);
        }).catch(error=>{
            resolve(error.response);
        });
    });
}

export const postGuardarEmpresa = model =>{
    return new Promise((resolve, eject) => {
        httpCliente.post('/empresa/postGuardarEmpresa', model)
        .then(response=>{
            resolve(response);
        }).catch(error=>{
            resolve(error.response);
        });
    });
}