import httpCliente from '../http/httpCliente';

export const getListarPlan = model =>{
    return new Promise((resolve, eject) => {
        httpCliente.get('/planmodulo/getListarPlan')
        .then(response=>{
            resolve(response);
        }).catch(error=>{
            resolve(error.response);
        });
    });
}

export const getListarMenu = model =>{
    return new Promise((resolve, eject) => {
        httpCliente.get('/planmodulo/getListarMenu?'+ `id=${model.idPlan}`)
        .then(response=>{
            resolve(response);
        }).catch(error=>{
            resolve(error.response);
        });
    });
}

export const postGuardarPlanModulo = model =>{
    return new Promise((resolve, eject) => {
        httpCliente.post('/planmodulo/postGuardarPlanModulo', model)
        .then(response=>{
            resolve(response);
        }).catch(error=>{
            resolve(error.response);
        });
    });
}