import httpCliente from '../http/httpCliente';

export const getListarPlan = model =>{
    return new Promise((resolve, eject) => {
        httpCliente.get('/plan/getListarPlan?'+ `page=${model.page}&pages=${model.pages}&request=${model.request}`)
        .then(response=>{
            resolve(response);
        }).catch(error=>{
            resolve(error.response);
        });
    });
}

export const getObtenerPlan = model =>{
    return new Promise((resolve, eject) => {
        httpCliente.get('/plan/getObtenerPlan?'+ `id=${model.id}`)
        .then(response=>{
            resolve(response);
        }).catch(error=>{
            resolve(error.response);
        });
    });
}

export const postGuardarPlan = model =>{
    return new Promise((resolve, eject) => {
        httpCliente.post('/plan/postGuardarPlan', model)
        .then(response=>{
            resolve(response);
        }).catch(error=>{
            resolve(error.response);
        });
    });
}