import httpCliente from '../http/httpCliente';

export const getListarContrato = model =>{
    return new Promise((resolve, eject) => {
        httpCliente.get('/contrato/getListarContrato?'+ `page=${model.page}&pages=${model.pages}&request=${model.request}`)
        .then(response=>{
            resolve(response);
        }).catch(error=>{
            resolve(error.response);
        });
    });
}

