import React, {useState, useEffect} from 'react';
import {Container,Grid, TextField,BottomNavigation,BottomNavigationAction, Divider, Paper, Table, TableBody, TableCell,TableContainer, TableFooter, TableHead, TableRow, Button, DialogContent, DialogActions, Dialog, DialogTitle, FormControl, InputLabel, Select, MenuItem, Autocomplete, ButtonGroup, TablePagination, CardActions} from '@mui/material';
import {SaveOutlined, ArrowBackOutlined, AddCircleOutline, Search, EditOutlined, DeleteOutlined} from '@mui/icons-material';
import PeopleAltOutlinedIcon from '@mui/icons-material/PeopleAltOutlined';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import DatePicker from '@mui/lab/DatePicker';
import { Box } from '@mui/system';
import style from "../../tools/Style";
import {useStateValue } from '../../context/store';
import {useParams, useHistory } from 'react-router-dom';
import ConfirmDialog from '../../compenentes/ConfirmDialog'

import {postGuardarContrato, getObtenerContrato, getListarUsuarioLogueado, deleteUsuarioLogueado, deleteContratoDetalle} from "../../Services/ContratoService";

const FormCreateContrato = () => {
    let history = useHistory();
    const [{sesionUsuario}, dispatch]  = useStateValue();
    const {id} = useParams();

    const [open, setOpen] = useState(false);
    const [openUser, setOpenUser] = useState(false);
    const [fechaContrato, setFechaContrato] = useState(new Date());
    const [fechaInicio, setFechaInicio] = useState(null);
    const [fechaFin, setFechaFin] = useState(null);
    const [cliente, setCliente] = useState(null);
    const [modalCliente, setModalCliente] = useState(null);
    const [model1, setModel1] = useState({
        codigoContrato :'',  
        nombreContrato:'',
        ruc:'',
        razonSocial: '',
        listaContratoDet:[],
        listaCliente:[],
        listaPlan:[],
        listaModalidad:[],
        listaEstado:[]
    });     
    const [model2, setModel2] = useState({
        strIdContratoDet:'',
        strIdContrato:'',
        strRuc:'',
        strRazonSocial: '',
        strIdPlan:'',
        strNombrePlan:'',
        strNroLicencia:0,
        strNombreModalidad:'',
        strModalidad:'',
        strNombreEstado:'',
        strEstado:''
    });
    const [modelRequest, setModelRequest] = useState({
        request:'',
        page:0,
        pages: 5
    });
    const [modelResponse, setModelResponse] = useState({
        items:[],
        page:0,
        pages: 0,
        total:0
    });
    const [confirmDialog, setConfirmDialog] = useState({ isOpen: false, title: '', subTitle: '' })

    const modelMemoria1 = e =>{
        const {name, value} = e.target;
        setModel1(anterior=>({
            ...anterior,
            [name]:value
        }));
    }

    const modelMemoria2 = e =>{      
        if(e.target.name=="strIdPlan"){
            model2.strNombrePlan =model1.listaPlan.filter(y=>y.value==e.target.value)[0].text;  
            model2.strNroLicencia =model1.listaPlan.filter(y=>y.value==e.target.value)[0].intValor;  
        }
        if(e.target.name=="strModalidad"){
            model2.strNombreModalidad =model1.listaModalidad.filter(y=>y.value==e.target.value)[0].text;  
        }
        if(e.target.name=="strEstado"){
            model2.strNombreEstado =model1.listaEstado.filter(y=>y.value==e.target.value)[0].text;
        }
        const {name, value} = e.target;
        setModel2(anterior=>({
            ...anterior,
            [name]:value
        }));
        
    }

    const btnAbrirModal = (ids) => { 
        let modalDetalle ={};
        modalDetalle.strIdContratoDet='';
        modalDetalle.strIdContrato='';
        modalDetalle.strRuc='';
        modalDetalle.strRazonSocial='';
        modalDetalle.strIdPlan='';
        modalDetalle.strNombrePlan='';
        modalDetalle.strNroLicencia='';
        modalDetalle.strNombreModalidad='';
        modalDetalle.strModalidad='';
        modalDetalle.strNombreEstado='';
        modalDetalle.strEstado='';

        if(ids!=''){
            let dataDet= model1.listaContratoDet.filter(y=>y.ruc==ids)[0];        
            let cli = {
                value:dataDet.ruc,
                text:dataDet.razonSocial
            }

            modalDetalle.strIdContratoDet=dataDet.idContratoDet;
            modalDetalle.strIdContrato=dataDet.idContrato;
            modalDetalle.strRuc=dataDet.ruc;
            modalDetalle.strRazonSocial=dataDet.razonSocial;
            modalDetalle.strIdPlan=dataDet.idPlan;
            modalDetalle.strNombrePlan=dataDet.nombrePlan;
            modalDetalle.strNroLicencia=dataDet.nroLicencia;
            modalDetalle.strNombreModalidad=dataDet.nombreModalidad;
            modalDetalle.strModalidad=dataDet.modalidad;
            modalDetalle.strNombreEstado=dataDet.nombreEstado;
            modalDetalle.strEstado=dataDet.estado;
            
            setModel2(modalDetalle);            
            setModalCliente(cli);
            setFechaContrato(new Date(dataDet.fechaContrato));
            setFechaInicio(new Date(dataDet.fechaInicio));
            setFechaFin(new Date(dataDet.fechaFin));            
        }else{
            setModel2(modalDetalle);            
            setModalCliente(null);
            setFechaContrato(new Date());
            setFechaInicio(null);
            setFechaFin(null); 
        }  

        setOpen(true);
    };
    
    const btnAbrirModalUser = (ids) => {
        listarUsuarioLogueado(ids);
        setOpenUser(true);
    };

    const btnCerarModal = () => {
        setOpen(false);
    };

    const btnCerarModalUser = () => {
        setOpenUser(false);
    };

    const btnAgregarModal = () => {
        
        let dataDet= model1.listaContratoDet.filter(y=>y.ruc==modalCliente.value)[0]; 

        if(dataDet==undefined){
            let det={};
            det.idContratoDet=model2.strIdContratoDet;
            det.ruc=modalCliente.value;
            det.razonSocial=modalCliente.text;
            det.nombrePlan=model2.strNombrePlan;
            det.idPlan=model2.strIdPlan;
            det.nroLicencia=model2.strNroLicencia;
            det.strFechaContrato=fechaContrato.toLocaleDateString();
            det.strFechaInicio=fechaInicio.toLocaleDateString();
            det.strFechaFin=fechaFin.toLocaleDateString();
            det.fechaContrato=fechaContrato;
            det.fechaInicio=fechaInicio;
            det.fechaFin=fechaFin;
            det.nombreModalidad=model2.strNombreModalidad;
            det.modalidad=model2.strModalidad;
            det.nombreEstado=model2.strNombreEstado;
            det.estado=model2.strEstado;

            model1.listaContratoDet.push(det);
        }else{
            dataDet.idContratoDet=model2.strIdContratoDet;
            dataDet.ruc=modalCliente.value;
            dataDet.razonSocial=modalCliente.text;
            dataDet.nombrePlan=model2.strNombrePlan;
            dataDet.idPlan=model2.strIdPlan;
            dataDet.nroLicencia=model2.strNroLicencia;
            dataDet.strFechaContrato=fechaContrato.toLocaleDateString();
            dataDet.strFechaInicio=fechaInicio.toLocaleDateString();
            dataDet.strFechaFin=fechaFin.toLocaleDateString();
            dataDet.fechaContrato=fechaContrato;
            dataDet.fechaInicio=fechaInicio;
            dataDet.fechaFin=fechaFin;
            dataDet.nombreModalidad=model2.strNombreModalidad;
            dataDet.modalidad=model2.strModalidad;
            dataDet.nombreEstado=model2.strNombreEstado;
            dataDet.estado=model2.strEstado;
        }

        setOpen(false);
    };

    const btnGuardar = e =>{
        e.preventDefault();  
        dispatch({
            type : "OPEN_BACKDROP",
            open : true
        })

        model1.ruc=cliente.value;
        model1.razonSocial=cliente.text;
        postGuardarContrato(model1).then(response =>{ 
            dispatch({
                type : "OPEN_BACKDROP",
                open : false
            })        
            if(response.status === 200) {
                if(response.data.tipoRespuesta!=2){
                    btnRegresar();
                }               
            }
            dispatch({
                    type : "OPEN_SNACKBAR",
                    openMensaje : {
                        open : true,
                        mensaje : response.data.mensaje,
                        tipoRespuesta:response.data.tipoRespuesta
                    }
                })
            });
    }

    const btnEliminarUsuarioLogueado = (ids, user,ruc) =>{
        dispatch({
            type : "OPEN_BACKDROP",
            open : true
        })
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        })

        let modelUsuario ={};
        modelUsuario.idcontratodet=ids;
        modelUsuario.user=user;

        deleteUsuarioLogueado(modelUsuario).then(response =>{           
            if(response.status === 200) {
                listarUsuarioLogueado(ruc);
            }
            dispatch({
                type : "OPEN_BACKDROP",
                open : false
            })
            dispatch({
                type : "OPEN_SNACKBAR",
                openMensaje : {
                    open : true,
                    mensaje : response.data.mensaje,
                    tipoRespuesta:response.data.tipoRespuesta
                }
            })
        });
    }   

    const btnObtener =()=>{ 
        model1.id= (id==undefined || id==0)? "": id;
        getObtenerContrato(model1).then(response =>{            
            if(response.status === 200) {               
                setModel1(response.data);
                if(response.data.ruc!=null){
                    let cli = {
                        value:response.data.ruc,
                        text:response.data.razonSocial
                    }
                    setCliente(cli);
                }               
            }else{
                dispatch({
                    type : "OPEN_SNACKBAR",
                    openMensaje : {
                        open : true,
                        mensaje : "Hubo un problema en el sistema web",
                        tipoRespuesta: 0
                    }
                })
            }            
        }); 
    }

    const btnRegresar =()=>{
        history.push("/contrato");
    }

    const btnEliminarContratoDetalle =(ids,rucs)=> {
        let modelContrato ={};
        modelContrato.ruc=rucs;

        if(ids=="" || ids==null || ids==undefined){
            dispatch({
                type : "OPEN_BACKDROP",
                open : true
            })
            setConfirmDialog({
                ...confirmDialog,
                isOpen: false
            }) 

            let dataDet= model1.listaContratoDet.filter(y=>y.ruc!=rucs); 
            model1.listaContratoDet = dataDet;

            setModel1(model1);
            if(model1.ruc!=null){
                let cli = {
                    value:cliente.value,
                    text:cliente.text
                }
                setCliente(cli);
            }
            dispatch({
                type : "OPEN_BACKDROP",
                open : false
            })
            dispatch({
                type : "OPEN_SNACKBAR",
                    openMensaje : {
                        open : true,
                        mensaje : "El registro ha sido eliminado",
                        tipoRespuesta:1
                    }
            })

        }else{
            deleteContratoDetalle(modelContrato).then(response =>{
                dispatch({
                    type : "OPEN_BACKDROP",
                    open : true
                })
                setConfirmDialog({
                    ...confirmDialog,
                    isOpen: false
                })             
                if(response.status === 200) {
                    if(response.data.tipoRespuesta!=2){
                        let dataDet= model1.listaContratoDet.filter(y=>y.ruc!=rucs); 
                        model1.listaContratoDet = dataDet;
        
                        setModel1(model1);
                        if(model1.ruc!=null){
                            let cli = {
                                value:cliente.value,
                                text:cliente.text
                            }
                            setCliente(cli);
                        }
                    }                    
                }
                dispatch({
                    type : "OPEN_BACKDROP",
                    open : false
                })
                dispatch({
                    type : "OPEN_SNACKBAR",
                        openMensaje : {
                            open : true,
                            mensaje : response.data.mensaje,
                            tipoRespuesta:response.data.tipoRespuesta
                        }
                    })
                });
        }             
    }

    const btnCambiarPagina = (event, newpag) => {
        setModelRequest((anterior)=>({
            ...anterior,
            page: parseInt(newpag)
        }));
    }

    const btnCambiarCantidadPagina = (event) =>{
        setModelRequest((anterior)=>({
            ...anterior,
            pages: parseInt(event.target.value),
            page:0
        }));
    }

    const listarUsuarioLogueado = async(ids) =>{
        let newpage = modelRequest.page + 1;

        const model ={
            request: ids,
            page: newpage,
            pages: modelRequest.pages
        }

        const response = await getListarUsuarioLogueado(model);
        setModelResponse(response.data);
    };

    useEffect(() => {
        btnObtener();    
    }, []);

    return (
        <Box style={{width:"100%"}}>
            <Paper style={{zIndex: '100'}} sx={style.paperboton} elevation={3}>
                <BottomNavigation showLabels style={{paddingTop: '8px'}}>
                    <BottomNavigationAction style={{color: '#274c5e'}} label="Regresar" icon={<ArrowBackOutlined color="secondary"/>} onClick={btnRegresar} />
                    <BottomNavigationAction style={{color: '#274c5e'}} label="Nuevo" icon={<AddCircleOutline color="success"/>} />
                    <BottomNavigationAction style={{color: '#274c5e'}} label="Guardar" icon={<SaveOutlined color="primary"/>} type="submit" onClick={btnGuardar} />           
                </BottomNavigation>
            </Paper>
    
            <div style={style.form}> 
                <Divider>
                    <h3 style={{color: '#1976d2'}}>CONTRATO</h3> 
                </Divider>         
                <Container style={style.paper}>  
                    <form>
                        <Grid container spacing={1}>
                            <Grid item xs={12} md={12}>
                                <TextField name="codigoContrato" value={model1.codigoContrato} onChange={modelMemoria1} variant="outlined" fullWidth label="Cdigo Contrato" disabled={true}/>
                            </Grid>    
                            <Grid item xs={12} md={12}>
                                <TextField name="nombreContrato" value={model1.nombreContrato} onChange={modelMemoria1} variant="outlined" fullWidth label="Nombre Contrato"/>
                            </Grid>   
                            <Grid item xs={12} md={12}>
                                <Autocomplete        
                                    disablePortal
                                    id="combo-box-demo"
                                    options={model1.listaCliente}
                                    getOptionLabel={(option)=>`${option.value} - ${option.text}`}
                                    fullWidth
                                    renderInput={(params) => <TextField {...params} label="Razn Social" />}
                                    value={cliente}
                                    onChange={(_event, newTeam) => {
                                        setCliente(newTeam);
                                    }}
                                    
                                />                             
                            </Grid>                             
                            <Grid item xs={12} md={12}>
                                <Grid container justifyContent="right">
                                    <Grid item xs={4} sm={2} md={2}>                                        
                                        <Button fullWidth variant="outlined" color="primary" value={''} onClick={() => btnAbrirModal('')}>
                                            <Search /> Buscar
                                        </Button>
                                    </Grid>                                    
                                </Grid>                          
                            </Grid>
                            <Grid item xs={12} md={12}>
                            </Grid>
                        </Grid>                       
                    </form>
                </Container>
                <Paper sx={{ width: '100%', overflow: 'hidden' }}> 
                    <CardActions>              
                    <TableContainer sx={{ maxHeight: 400 }}>
                        <Table stickyHeader aria-label="sticky table">
                            <TableHead>
                                <TableRow>
                                    <TableCell style={style.thead} align="left">Ruc</TableCell>
                                    <TableCell style={style.thead} align="left">Razon Social</TableCell>  
                                    <TableCell style={style.thead} align="left">Plan</TableCell>                              
                                    <TableCell style={style.thead} align="left">Licencia</TableCell>
                                    <TableCell style={style.thead} align="left">F. Contrato</TableCell>
                                    <TableCell style={style.thead} align="left">F. Inicio</TableCell>     
                                    <TableCell style={style.thead} align="left">F. Fin</TableCell>      
                                    <TableCell style={style.thead} align="left">Modalidad</TableCell>         
                                    <TableCell style={style.thead} align="left">Estado</TableCell>  
                                    <TableCell style={style.thead} align="left">Accin</TableCell>                          
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {model1.listaContratoDet==null? null: model1.listaContratoDet.map((row) =>(
                                    <TableRow key ={row.ruc}>
                                        <TableCell style={style.tbody} align="left">{row.ruc}</TableCell>
                                        <TableCell style={style.tbody} align="left">{row.razonSocial}</TableCell>
                                        <TableCell style={style.tbody} align="left">{row.nombrePlan}</TableCell>  
                                        <TableCell style={style.tbody} align="left">{row.nroLicencia}</TableCell>
                                        <TableCell style={style.tbody} align="left">{row.strFechaContrato}</TableCell> 
                                        <TableCell style={style.tbody} align="left">{row.strFechaInicio}</TableCell> 
                                        <TableCell style={style.tbody} align="left">{row.strFechaFin}</TableCell>  
                                        <TableCell style={style.tbody} align="left">{row.nombreModalidad}</TableCell>
                                        <TableCell style={style.tbody} style={style.tbody} align="left">{row.nombreEstado}</TableCell>
                                        <TableCell style={style.tbody} align="left">
                                            <ButtonGroup variant="text" aria-label="text button group">
                                                <Button value={row.idContratoDet} onClick={() => btnAbrirModalUser(row.ruc)}><PeopleAltOutlinedIcon color="success" /></Button>
                                                <Button value={row.idContratoDet} onClick={() => btnAbrirModal(row.ruc)}><EditOutlined color="primary" /></Button>
                                                <Button 
                                                    value={row.idContratoDet} 
                                                    onClick={() => {
                                                        setConfirmDialog({
                                                            isOpen: true,
                                                            title: 'Ests segura de eliminar este registro?',
                                                            subTitle: "Despues de eliminar no podras recuperar",
                                                            onConfirm: () => { btnEliminarContratoDetalle(row.idContratoDet, row.ruc) }
                                                        })
                                                    }}
                                                    >
                                                        <DeleteOutlined color="error" />
                                                </Button>
                                            </ButtonGroup>                                                           
                                        </TableCell> 
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>
                    </CardActions>
                </Paper>
            </div>   

            <Dialog open={open} onClose={btnCerarModal}>
                <DialogTitle>Detalle Contrato</DialogTitle>
                <DialogContent>
                <Grid container spacing={1} style={{paddingTop:10, width:"100%"}}>
                    <Grid item xs={12} md={12}>
                        <Autocomplete        
                            disablePortal
                            id="combo-box-demo"
                            options={model1.listaCliente}
                            getOptionLabel={(option)=>`${option.value} - ${option.text}`}
                            fullWidth
                            renderInput={(params) => <TextField {...params} label="Cliente" />}
                            value={modalCliente}
                            onChange={(_event, newTeam) => {
                                setModalCliente(newTeam);
                            }}
                        />  
                    </Grid> 
                    <Grid item xs={12} md={12}>
                        <LocalizationProvider dateAdapter={AdapterDateFns}>
                            <DatePicker label="Fecha Contrato" value={fechaContrato} onChange={(x) => { setFechaContrato(x); }} renderInput={(params) => <TextField fullWidth {...params} />}/>
                        </LocalizationProvider>                    
                    </Grid> 
                    <Grid item xs={12} md={10}>  
                        <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label">Plan</InputLabel>
                            <Select variant="outlined" name="strIdPlan" value={model2.strIdPlan} label="Plan" onChange={modelMemoria2}>
                            {model1.listaPlan.map((name) => (
                                <MenuItem
                                    key={name.value}
                                    value={name.value}
                                    >
                                    {name.text}
                                </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </Grid>    
                    <Grid item xs={12} md={2}>
                        <TextField name="strNroLicencia" value={model2.strNroLicencia}  onChange={modelMemoria2} variant="outlined" fullWidth label="Licencia"/>
                    </Grid> 
                    <Grid item xs={12} md={6}>   
                        <LocalizationProvider dateAdapter={AdapterDateFns}>
                            <DatePicker label="Fecha Inicio" value={fechaInicio} onChange={(x) => { setFechaInicio(x); }} renderInput={(params) => <TextField fullWidth {...params} />}/>
                        </LocalizationProvider>               
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <LocalizationProvider dateAdapter={AdapterDateFns}>
                            <DatePicker label="Fecha Fin" value={fechaFin} onChange={(x) => { setFechaFin(x); }} renderInput={(params) => <TextField fullWidth {...params} />}/>
                        </LocalizationProvider> 
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label">Modalidad</InputLabel>
                            <Select variant="outlined" name="strModalidad" value={model2.strModalidad} label="Modaliad" onChange={modelMemoria2}>
                            {model1.listaModalidad.map((name) => (
                                <MenuItem
                                    key={name.value}
                                    value={name.value}
                                    >
                                    {name.text}
                                </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label">Estado</InputLabel>
                            <Select variant="outlined" name="strEstado" value={model2.strEstado} label="Estado" onChange={modelMemoria2}>
                            {model1.listaEstado.map((name) => (
                                <MenuItem
                                    key={name.value}
                                    value={name.value}
                                    >
                                    {name.text}
                                </MenuItem>
                                ))}
                            </Select>
                        </FormControl>                 
                     </Grid>
                </Grid>
                </DialogContent>
                <DialogActions>
                    <Button onClick={btnCerarModal}>Cancel</Button>
                    <Button onClick={btnAgregarModal}>Agregar</Button>
                </DialogActions>
            </Dialog> 

            <Dialog open={openUser} onClose={btnCerarModalUser}>
                <DialogTitle>Usuarios Logueados</DialogTitle>
                <DialogContent>
                    <Grid spacing={1} style={{paddingTop:10, width:"100%"}}>
                         
                            <TableContainer sx={{ maxHeight: 400 }}>
                                <Table stickyHeader aria-label="sticky table">
                                    <TableHead>
                                        <TableRow>
                                            <TableCell style={style.thead} align="left">Usuario</TableCell>
                                            <TableCell style={style.thead} align="left">Nombre Completo</TableCell>  
                                            <TableCell style={style.thead} align="left">F. Logueo</TableCell> 
                                            <TableCell style={style.thead} align="left">Accin</TableCell>                          
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                    {modelResponse.items==null? null: modelResponse.items.map((row) =>(
                                        <TableRow key ={row.idUsuarioLogeado}>
                                            <TableCell style={style.tbody} align="left">{row.userName}</TableCell>
                                            <TableCell style={style.tbody} align="left">{row.nombreCompleto}</TableCell>
                                            <TableCell style={style.tbody} align="left">{(new Date(row.fechaLogeo)).toLocaleString()}</TableCell>
                                            <TableCell style={style.tbody} align="left">
                                                <ButtonGroup variant="text" aria-label="text button group">                                               
                                                    <Button 
                                                     onClick={() => {
                                                        setConfirmDialog({
                                                            isOpen: true,
                                                            title: 'Ests segura de eliminar este registro?',
                                                            subTitle: "Despues de eliminar no podras recuperar",
                                                            onConfirm: () => { btnEliminarUsuarioLogueado(row.idContratoDet, row.userName, row.ruc) }
                                                            })
                                                        }}
                                                        >
                                                            <DeleteOutlined color="error" />
                                                        </Button>
                                                </ButtonGroup>                                                           
                                            </TableCell>
                                        </TableRow>
                                    ))} 
                                    </TableBody>
                                    <TableFooter>                                
                                    </TableFooter>
                                </Table>
                            </TableContainer>
                            <TablePagination 
                                    component="div" 
                                    rowsPerPageOptions={[5, 10, 25]}
                                    count={modelResponse.total}
                                    rowsPerPage={modelRequest.pages}
                                    page={modelRequest.page}
                                    onPageChange={btnCambiarPagina}
                                    onRowsPerPageChange={btnCambiarCantidadPagina}
                                    labelRowsPerPage="Paginacin"
                                />
                       
                    </Grid>
                </DialogContent>
                <DialogActions>
                    <Button onClick={btnCerarModalUser}>Cancel</Button>
                </DialogActions>
            </Dialog> 

            <ConfirmDialog
                confirmDialog={confirmDialog}
                setConfirmDialog={setConfirmDialog}
            />                             
        </Box>      
      );
    };

    
export default FormCreateContrato;